.zorb-remix-view {
    position: absolute;
    overflow-x: hidden;
    z-index: 10000000000;
    top: 0%;
    left: 0px;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    //background: linear-gradient(45deg, rgb(67 0 255 / 80%) 0%, rgb(163 198 250 / 60%) 50%, rgba(107, 67, 184, 0.55) 100%);
    //backdrop-filter: blur(20px);
    background: linear-gradient(45deg, rgb(67 0 255 / 95%) 0%, rgb(163 198 250 / 98%) 50%, rgb(107 67 184 / 96%) 100%);
//    animation-name: slide-up;
//    animation-duration: 1s;
    display: flex;
    flex-direction: column;
    max-height: 100vh;
    overflow-y: scroll;
    padding-bottom: 100px;

    .back-btn {
        z-index: 10000;
        position: absolute;
        top: 35px;
        left: 35px;
        transform: scale(3);
        transform-origin: top left;
        cursor: pointer;
        width: 32px;
        height: 32px;
    }

    .ancestral-source {
        //box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        align-items: flex-start;
        //background-color: black;
        margin-top: 20px;
        padding: 10px;
        padding-left: 30px;
        padding-right: 30px;
        margin-left: auto;
        margin-right: auto;
        border-radius: 10px;
        z-index: 2;
        display: flex;
        .artist-name {
            color: gray;
        }
        .song-name {
            margin-right: 30px;
        }
    }

    .zorb-children {
        padding-left: 20px;
        margin-left: 100px;
        margin-right: 100px;
        margin-top: 100px;
        flex: 1;
        // overflow-y: scroll;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: flex-start;
        z-index: 2;
        align-content: flex-start;

        .zorb-remix-option.playing {
            background-color: #ffffff29;
            animation-name: pad-flash;
            animation-duration: 1s;
            position: relative;

            .pause-button {
                left: 0px;
                right: 0px;
                margin-left: auto;
                margin-right: auto;
                top: 15px;
                position: absolute;
                width: 30px;
                height: 50px;
                border-style: double;
                border-width: 0px 0px 0px 37px;
                border-color: white;
            }
        }

        .zorb-remix-option {
            // width: 200px;

            //box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
            align-items: flex-start;
            padding: 10px;
            min-height: 100px;
            min-width: 90px;
            margin: 10px;
            margin-bottom: 30px;
            //background-color: #ffffff29;
            //border: 2px solid #ffffff26;
            color: black;
            border-radius: 8px;
            position: relative;

            .token-id {
                position: absolute;
                z-index: 1000;
                font-size: 8pt;
                left: 8px;
                top: 8px;
            }
            .caption {
                img {
                    width: 60px;
                    height: 60px;
                }
                font-size: 0pt !important;
            }
            .inner {
                width: 100%;
                display: flex;
                flex-direction: row;
                margin-bottom: 5px;
            }

            .remixes-count {
                background-color: #ffffff6e;
                transform: scale(0.75);
            }
            
            display: flex;
            flex-direction: column;
                                   
            .hover-name.show {
                display: block;
            }

            .hover-name {
                display: none;
            }
            span.ens-name {
                position: absolute;
                color: white;
                font-size: 10pt;
                z-index: 1;
                background-color: #ffffffb0;
                backdrop-filter: blur(8px);
                color: black;
                padding: 5px 15px 5px 15px;
                bottom: 0px;
                top: unset;
                left: 0px;
            }

            // height: 80px;

            .hover-name {
//                position: absolute;
            }
            
            .caption::before {
                content: "";
                width: 16px;
                position: absolute;
                height: 16px;
                left: -16px;
                top: 7px;
                border: 8px solid red;
                border-color: transparent  #097aff transparent transparent;
            }
            .caption {
                position: relative;
                padding: 5px;
                background-color: #097aff;
                color: white;
                font-size: 9pt;
                border-radius: 8px;
                margin-left: 20px;
            }
            .zorb-image {
                margin-left: auto;
                margin-right: auto;
                margin-top: 15px;
                height: 40px;
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .zorb-remix-view {

        .back-btn {
            left: 15px;
        }
        .zorb-children {
            padding: 0px;
            padding-top: 50px;
            margin: 0px;
            margin-left: 8%;
            .zorb-remix-option {
                margin: 3vw;
                //width: 44vw;
            }
        }
    }
}

@keyframes slide-up {
    from {
        top: 100%;
    }
    to {
        top: 0%;
    }
}

@keyframes pad-flash {
    from {
        background-color: red;
    }

    to {
        background-color: #ffffff29;
    }
}

