.zorb-sample-tooltip {
    border-top-right-radius: 12px;
    border-top-left-radius: 12px;
    
    white-space: nowrap;
    animation-name: disappear;
    animation-duration: 4s;
    opacity: 0;
    transform: scale(0.75);
    bottom: -8px !important;
    background-color: #ffffff33 !important;
    overflow: hidden;
    backdrop-filter: blur(8px);
}

@keyframes disappear {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
