.remix-details {
    .logo-small {
        //transform: rotate(90deg);
        position: absolute;
        top: 25px;
        right: 25px;
        width: 100px;
        object-fit: fill;
    }
}
.remix-dna-details {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    align-items: flex-start;
    background-color: black;
    border-radius: 10px;
    border: 2px solid #393737b3;
    box-shadow: 0px 0px 4px 4px #00000059;
    border-radius: 10px;
    width: 220px;
    height: 220px;
    margin-top: 20px;
    
    .contract-container {
        padding: 8px;
        background-color: #7a74743d;
        border-radius: 100px;
        overflow: hidden;
        margin: 3px;
        border: 1px solid #ffffff05;
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        align-items: flex-start;

        .remix-atom {
            margin: 2px;
            filter: brightness(2);
            background-image: linear-gradient(45deg, black -10px, transparent);
        }
    }
    .remix-bpm {
        background-color: #7a74743d;
    }
}
