.fullscreen-backdrop {
    position: absolute;
    top: 0px;
    background-color: #40404066;;
    backdrop-filter: blur(16px);
    left: 0px;
    right: 0px;
    bottom: 0px;
    width: 100%;
    height: 100%;
    z-index: 1000000000000;
    display: flex;
    overflow: hidden;

    .remix-details {
        margin: auto;
        width: 380px;
        min-height: 400px;
        max-height: 400px;
    }
    
    .name-text {
        color: #8b8bb24f;;
        font-size: 28px;
        margin-bottom: 25px !important;
        white-space: nowrap;
        max-width: 95%;
        overflow: hidden;
    }

    .token-text {
        color: #8b8bb24f;
        font-size: 40px;
    }

    .inner-details {
        padding-top: 30px !important;
    }

    .fullscreen-gen {
        border-radius: 30px;
        margin: auto;
        height: 500px;
        width: 500px;
        background-color: #00000061;
    }
}


.currently-playing {
    display: flex;
    background-color: rgb(255 255 255 / 21%);
    background-image: linear-gradient(218deg, rgb(17 32 51 / 6%), transparent);
    position: fixed;
    backdrop-filter: blur(8px);
    width: 100%;
    height: 80px;
    z-index: 10000000000;
    color: white;
    bottom: 0px;

    .scrub-bar {
        position: relative;
        margin-left: 20px;
        margin-right: 80px;
        margin-top: auto;
        margin-bottom: auto;
        border-radius: 8px;
        background-color: #00000029;

        .playhead {
            top: -8px;
            position: absolute;
            border-radius: 12px;
            width: 22px;
        }
    }

    .play-btn-wrapper {
        border: unset !important;
        background-color: transparent !important;
        margin-top: -2px;
        width: 100px;

        .play-btn.playing {
            border-width: 0px 0px 0px 30px;
            height: 40px;
        }

        .play-btn {
            margin-top: unset;
            height: 40px;
            border-width: 20px 0px 20px 35px;
        }
    }
    .gen-img {
        display: none;
        margin-left: 15px;
        margin-top: auto;
        margin-bottom: auto;
        width: 80px;
        height: 80px;
    }

    .ens-name {
        margin-top: auto;
        margin-bottom: auto;
        margin-left: 15px;
        margin-right: 15px;
        color: white;
    }

    img.zorb {
        margin-left: auto;
        margin-top: 20px;
        width: 40px;
        height: 40px;
    }
    svg {
        margin-top: 3px;
        circle {
            fill: transparent;
        }
    }
}


@media screen and (max-width: 600px) {
    .currently-playing {
        height: 60px;

        img.zorb {
            margin-top: 10px;
        }

        .gen-img {
            width: 50px;
            height: 50px;
        }

        svg {
            margin-top: -3px;
            transform: scale(0.8);
            circle {
                fill: transparent;
            }
        }

    }
    .fullscreen-backdrop {
        .fullscreen-gen {
            border-radius: 30px;
            margin: auto;
            width: 70%;
            height: unset;
            background-color: black;
        }
    }

    .currently-playing {
        .scrub-bar {
            margin-left: 0px !important;
            margin-right: 10px !important;
        }
        .play-btn-wrapper {
            border: unset !important;
            background-color: transparent !important;
            margin-top: 0px !important;
            width: 30px;
            max-width: 50px;
            min-width: 50px;
            
            .play-btn.playing {
                border-width: 0px 0px 0px 20px;
                height: 20px;
            }
            
            .play-btn {
                margin-top: unset;
                height: 20px;
                border-width: 10px 0px 10px 20px;
        }
        }
    }
}
